import React from "react";
import styled from "styled-components";

const StyledSvg = styled.svg`
  padding-left: 10px;

  #whatis-boat {
    transform-origin: 230px 400px;
    animation: what-is-boat 8s linear infinite;
  }
  @keyframes what-is-boat {
    0% {
      transform: scale(1);
    }
    50% {
      transform: rotate3d(0,0,1,-9deg);
    }
    100% {
      transform: scale(1);
    }
  }
  
  #whatis-star {
    &-top {
      transform-origin: 326px 73px;
      animation: whatis-star 6s linear infinite;
    }
    &-right {
      transform-origin: 468px 111px;
      animation: whatis-star 4s linear infinite;
    }
    &-bottom {
      transform-origin: 465px 254px;
      animation: whatis-star-reverse 6s linear infinite;
    }
  }
  @keyframes whatis-star {
    0% {
      opacity: 0;
      transform: scale(1);
    }
    50% {
      opacity: 1;
      transform: scale(1.1) rotate3d(0,0,1,20deg);
    }
    100% {
      opacity: 0;
      transform: scale(1);
    }
  }
  @keyframes whatis-star-reverse {
    0% {
      opacity: 0;
      transform: scale(1);
    }
    50% {
      opacity: 1;
      transform: scale(1.1) rotate3d(0,0,1,-20deg);
    }
    100% {
      opacity: 0;
      transform: scale(1);
    }
  }
`;

